html{font-size:14px}body{margin:0;background-color:#535353;font-family:"Courier New", Courier, monospace;font-weight:bold;color:#fff;-webkit-font-smoothing:alias;-moz-osx-font-smoothing:grayscale;overflow-y:scroll}body,aside,div,section,article{scrollbar-width:auto;scrollbar-color:#535353 #c6c6c6}body::-webkit-scrollbar,aside::-webkit-scrollbar,div::-webkit-scrollbar,section::-webkit-scrollbar,article::-webkit-scrollbar{width:1rem}body::-webkit-scrollbar-track,aside::-webkit-scrollbar-track,div::-webkit-scrollbar-track,section::-webkit-scrollbar-track,article::-webkit-scrollbar-track{box-shadow:none;background-color:#535353}body::-webkit-scrollbar-thumb,aside::-webkit-scrollbar-thumb,div::-webkit-scrollbar-thumb,section::-webkit-scrollbar-thumb,article::-webkit-scrollbar-thumb{background-color:#c6c6c6;outline:none}img,svg{vertical-align:bottom}a{text-decoration:none;color:#fff}a:hover{color:#5caedf}

.home-page .to-top{position:fixed;bottom:0.5rem;right:0.5rem;color:#fff;font-size:3rem}.home-page .to-top:hover{cursor:pointer}.home-page .page-nav{position:fixed;bottom:0;left:50%;transform:translateX(-50%);background-color:#0009;padding:0.5rem;z-index:20;margin:0;-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);width:min(95ch, 100%);box-sizing:border-box}.home-page .page-nav ul{list-style:none;padding:0;margin:0;display:flex;align-items:center;justify-content:space-between}.home-page .page-nav ul>:first-child,.home-page .page-nav ul>:last-child{font-size:2rem;line-height:1em}.home-page .page-nav ul li{margin:0 0.5rem}.home-page .page-nav ul li:first-child,.home-page .page-nav ul li:last-child{color:#aaa}.home-page .page-nav ul li:first-child svg,.home-page .page-nav ul li:last-child svg{pointer-events:none}.home-page .page-nav ul li:first-child.enabled,.home-page .page-nav ul li:last-child.enabled{color:#fff}.home-page .page-nav ul li:first-child.enabled:hover,.home-page .page-nav ul li:last-child.enabled:hover{cursor:pointer}.home-page .page-nav ul li.pages{display:flex;overflow:hidden;flex-wrap:nowrap;color:#fff}.home-page .page-nav ul li.pages button{border:0;padding:0;background:transparent;font-family:inherit;font-size:inherit;font-weight:inherit;color:inherit;margin:0 0.5rem;outline:none}.home-page .page-nav ul li.pages button:hover{cursor:pointer;text-decoration:underline}.home-page .page-nav ul li.pages button.current{text-decoration:underline}.home-page .search-form{grid-column:3;grid-row:2;width:100%;margin-top:3rem}.home-page .search-form .search-term-label{position:relative;display:block;background-color:#ff9b00;padding:0.4rem 0.3rem;margin-top:2rem;border:0.1rem solid #0004;box-shadow:1px 1px #af4b00, 2px 2px #af4b00, 3px 3px #af4b00, 4px 4px #af4b00, 5px 5px #af4b00}.home-page .search-form .search-term-label::before{content:">";position:absolute;top:50%;left:1.5rem;height:1em;transform:translateY(-50%);margin:0;font-size:1rem;font-family:"Courier New"}.home-page .search-form .search-input{width:100%;outline:none;background-color:black;color:#fff;box-sizing:border-box;border:0.2rem solid #ffe000;padding:0.5rem 2.5rem 0.5rem 2.5rem;font-family:"Courier New";font-weight:bold;font-size:1rem}.home-page .search-form .search-input::-webkit-input-placeholder{color:#fff9}.home-page .search-form .search-input:-ms-input-placeholder{color:#fff9}.home-page .search-form .search-input::-ms-input-placeholder{color:#fff9}.home-page .search-form .search-input::placeholder{color:#fff9}.home-page .search-form .search-button{background:transparent;position:absolute;border:0;padding:0;font-size:2rem;right:1rem;top:50%;transform:translateY(-50%);height:1em;overflow:hidden;outline:none;color:white}.home-page .search-form .search-button svg{vertical-align:baseline}.home-page .search-form .search-button:hover{cursor:pointer}.home-page .results-info{grid-column:2 / -2;grid-row:3;width:100%;box-sizing:border-box;padding:0.5rem;display:block;position:relative;margin-top:1rem}@media screen and (min-width: 470px){.home-page .results-info{display:flex;justify-content:space-between;align-items:center}}.home-page .results-info .total-results{display:none}@media screen and (min-width: 470px){.home-page .results-info .total-results{display:block}}.home-page .results-info .results-per-page{display:flex;align-items:center;justify-content:space-between;text-align:left}@media screen and (min-width: 460px){.home-page .results-info .results-per-page{justify-content:none}}.home-page .results-info .results-per-page .results-input-wrapper{position:relative;display:block;background-color:#ff9b00;padding:0.4rem 0.3rem;margin-left:1rem;border:0.1rem solid #0004;box-shadow:1px 1px #af4b00, 2px 2px #af4b00, 3px 3px #af4b00, 4px 4px #af4b00, 5px 5px #af4b00}.home-page .results-info .results-per-page .results-input-wrapper .results-input{width:100%;outline:none;background-color:black;color:#fff;box-sizing:border-box;border:0.2rem solid #ffe000;padding:0.5rem;font-family:"Courier New";font-weight:bold;font-size:1rem;width:10ch}.home-page .result-grid{display:grid;grid-template-columns:repeat(1, 1fr);grid-gap:1rem;width:100%;grid-column:2 / -2;grid-row:4;margin-top:1rem;margin-bottom:2rem}@media screen and (min-width: 420px){.home-page .result-grid{grid-template-columns:repeat(2, 1fr)}}@media screen and (min-width: 640px){.home-page .result-grid{grid-template-columns:repeat(3, 1fr)}}@media screen and (min-width: 760px){.home-page .result-grid{grid-template-columns:repeat(4, 1fr)}}.home-page .result-grid .no-results{position:relative;grid-column:1 / -1}.home-page .result-grid .search-result{width:100%;position:relative;height:-webkit-max-content;height:-moz-max-content;height:max-content}.home-page .result-grid .search-result:hover{cursor:pointer}.home-page .result-grid .search-result img,.home-page .result-grid .search-result object{width:100%}.home-page .result-grid .search-result .search-result-link{position:absolute;bottom:0;right:0;display:none;width:1em;height:1em;background-color:#0009;padding:0.5em;font-size:1.5rem}.home-page .result-grid .search-result .search-result-link svg{vertical-align:baseline}.home-page .result-grid .search-result:hover .search-result-link{display:inline-block}

.background{position:fixed;top:0;left:0;right:0;bottom:0;background-color:#5e5e5e}.background::after{content:"";width:100%;height:100%;background-color:#404040;position:absolute;top:0;left:0;right:0;bottom:0;opacity:0.75}

.main{display:flex;flex-direction:column;text-align:center}.main .main-middle{margin:0 auto;padding:1rem 2rem 2rem 2rem;box-sizing:border-box;width:100%;flex-grow:1;position:relative;display:grid;grid-template-columns:1fr 1fr min(95ch, 100%) 1fr 1fr}.main .system,.main .user{display:none}

.sidebar{position:fixed;left:0;top:0;height:100%;text-align:left;box-sizing:border-box;width:min(35ch, 100%);background-color:#000a;color:#fff;transform:translateX(-100%);display:flex;flex-direction:column;transition:transform 0.5s ease}.sidebar .sidebar-controls{width:100%;box-sizing:border-box;padding:1rem;display:flex;justify-content:space-between;transform:translateX(100%);background-color:#0009;transition:inherit;flex-shrink:0}.sidebar .sidebar-controls.close{transform:none}.sidebar .sidebar-button{border:0;padding:0;color:#fff;background:transparent;font-size:2rem;outline:none;z-index:10;display:flex;align-items:center;line-height:1}.sidebar .sidebar-button h2{font-size:2rem;margin:0;font-family:"Courier New";font-weight:bold}.sidebar .sidebar-button svg{transition:transform 0.5s ease}.sidebar .sidebar-button.open svg{transform:rotate(180deg)}.sidebar .sidebar-button:hover{cursor:pointer}.sidebar .sidebar-inner{padding:2rem 1rem 2rem 1rem;overflow-y:scroll;width:100%;height:100%;flex-grow:1;box-sizing:border-box}.sidebar.visible{transform:none}.sidebar ul{list-style:none;padding:0;margin:0;font-size:1.5rem}.sidebar ul ul{padding-left:1rem;padding-top:0.5rem;border-top:2px solid #fff;margin-bottom:1rem}.sidebar .sidebar-text{font-size:1rem}.sidebar .sidebar-text li{position:relative;color:#c6c6c6;text-transform:capitalize}.sidebar .sidebar-text.focusable li{transition:padding 0.25s ease, opacity 0.25s ease}.sidebar .sidebar-text.focusable li:hover{cursor:pointer;padding-left:1ch;color:#fff}

